import { useTranslation } from "gatsby-plugin-react-i18next";

import { Edition } from "../../data";
import { members, collaborators, MemberData } from "../general/team";

import * as styles from "./team.module.css";

type TeamMemberProps = {
  item: MemberData;
};

const TeamMember: React.FC<TeamMemberProps> = ({ item }) => {
  const [image, name, team, background, contact] = item;
  return (
    <div className={styles.teamMember}>
      <img src={image} alt={name} className={styles.memberImage} />
      <h4 className={styles.memberName}>{name}</h4>
      <p className={styles.memberTeam}>{team}</p>
      <p className={styles.memberDescription}>
        {background == "" ? "TA @University of Bucharest" : background}
      </p>
      <a
        href={"https://www.linkedin.com/in" + contact}
        target={"_blank"}
        rel={"noreferrer"}
      >
        <p className={`a ${styles.contactLink}`}>{contact}</p>
      </a>
    </div>
  );
};

type OurTeamProps = {
  edition: Edition;
};

const OurTeam: React.FC<OurTeamProps> = ({ edition }) => {
  const { t } = useTranslation();

  return (
    <div className={`page ${styles.team}`} id={"team"}>
      <h1 className={`title ${styles.teamTitle}`}>{t("title")}</h1>

      <h2 className={styles.teamSubtitle}>{t("members")}</h2>
      <div className={styles.membersList}>
        {members.map(
          (item, index) =>
            item[5].includes(edition) && (
              <TeamMember key={"teamMember" + index} item={item} />
            )
        )}
      </div>

      <h3 className={styles.teamSubtitle}>{t("collaborators")}</h3>
      <div className={styles.collaboratorsList}>
        {collaborators[edition].map((item, index) => (
          <p key={index} className={styles.collaboratorName}>
            {item}
          </p>
        ))}
      </div>
    </div>
  );
};

export default OurTeam;
